@use 'variables' as var;

h1, h2, h3 {
    font-family: var.$headings_font;
    margin: 0 0 calc(var.$gap / 2) 0 ;
    font-weight: 900;
}

h1 {
    font-size: 6rem;
}

h2 {
    font-size: 5rem;
}

h3 {
    font-size: 4rem;
}

p {
    font-size: 2rem;
    color: var.$grey;
    line-height: 1.8;
}

.description-page {
    text-align: center;
}

.tagline {
    font-family: var.$headings_font;
    font-weight: 900;
    color: var.$black;
    font-size: 3rem;
    text-align: center;
}