@use 'variables' as var;

*, *:before, *:after {
    box-sizing: inherit;
}

html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}

body {
    font-family: var.$text_font;
    font-size: 1.6rem;
    min-height: 100%;
}

.container {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}

.container-sm {
    max-width: 70rem;
    margin: 0 auto;
}

a {
    text-decoration: none;
}

img {
    max-width: 100%;
}